import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { UserService } from '../../angular-services/user.service';

@Component({
  selector: 'app-new-user-form-admin',
  templateUrl: './new-user-form.component.html',
  styleUrls: ['./new-user-form.component.css']
})
export class NewUserFormComponent implements OnInit {

  showPassword: boolean = false;
  formNewUser: FormGroup;
  alert: any = { type: '', message: '', show: false };

  constructor(private userService: UserService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formNewUser = new FormGroup({
      user: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewUser.valid) {
      let data = this.formNewUser.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.userService.addUser(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  changeTypeInputPassword() {
    if (this.showPassword) {
      this.showPassword = false;
    } else {
      this.showPassword = true;
    }
  }

}
